define('tiny/admin/subscriptions/new/controller', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend({
    users: (function () {
      return this.store.findAll('user');
    }).property('users'),
    courses: (function () {
      return this.store.findAll('course');
    }).property('courses')
  });

});